<template>
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="平台订单号">
                <el-input 
                  v-model="searchForm.platform_order_no" 
                  placeholder="请输入平台订单号"
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户订单号">
                <el-input 
                  v-model="searchForm.merchant_order_no" 
                  placeholder="请输入商户订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6" v-if="!userInfo?.is_merchant_admin">
              <el-form-item label="商户">
                <el-select 
                  v-model="searchForm.merchant_id" 
                  placeholder="请选择商户" 
                  clearable
                  filterable
                  style="width: 100%"
                >
                  <el-option 
                    v-for="merchant in merchantList" 
                    :key="merchant.id" 
                    :label="merchant.merchant_name" 
                    :value="merchant.id" 
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="支付状态">
                <el-select 
                  v-model="searchForm.pay_status" 
                  placeholder="请选择订单状态" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="全部" :value="''" />
                  <el-option label="已创建" :value="0" />
                  <el-option label="已打开" :value="4" />
                  <el-option label="已支付" :value="1" />
                  <el-option label="已关闭" :value="2" />
                  <el-option label="已退款" :value="3" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="时间范围">
                <el-date-picker
                  v-model="dateRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  format="YYYY-MM-DD HH:mm:ss"
                  value-format="YYYY-MM-DD HH:mm:ss"
                  style="width: 100%"
                  @change="handleDateRangeChange"
                  clearable
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch">
                    <el-icon><Search /></el-icon>搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>

  <!-- 表头/按钮行 -->
  <div class="table-header ba-scroll-style">
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <el-button type="primary" @click="handleBatchCallback" :disabled="selectedRows.length===0">一键回调</el-button>
    <el-button type="warning" @click="handleBatchSupplement" :disabled="selectedRows.length===0">一键补单</el-button>
    <!-- 订单统计条 - 精简版横向展示 -->
    <div class="table-statistics-bar">
      <span class="stat stat-amt">总金额: <b class="stat-num">¥{{ statistics.total_amount ? statistics.total_amount : 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat">总订单: <b class="stat-num">{{ statistics.total_count || 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-amt">成交额: <b class="stat-num">¥{{ statistics.paid_amount ? statistics.paid_amount : 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-succ">成功订单量: <b class="stat-num">{{ statistics.paid_count || 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-amt">官方手续费: <b class="stat-num">¥{{ statistics.official_fee ? statistics.official_fee : 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-rate">成功率: <b class="stat-num">{{ statistics.success_rate || 0 }}%</b></span>
      <span class="split">|</span>
      <span class="stat">一分钟订单数: <b class="stat-num">{{ statistics.last_minute_count || 0 }}</b></span>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="40"/>
      <!-- 表格列定义 -->
      <el-table-column prop="agent.agent_name" label="代理商" align="center" min-width="120" v-if="!isAgent"/>
      <el-table-column prop="merchant.merchant_name" label="商户" align="center" min-width="150"/>
      <el-table-column prop="platform_order_no" label="平台订单号" align="center" min-width="200"/>
      <el-table-column prop="merchant_order_no" label="商户订单号" align="center" min-width="180"/>
      
      <!-- 支付主体列 -->
      <el-table-column label="支付主体" align="center" min-width="150">
        <template #default="{ row }">
          {{ row.subject?.company_name || '-' }}
        </template>
      </el-table-column>
      
      <el-table-column prop="order_amount" label="订单金额" align="center" width="90">
        <template #default="{ row }">
          ¥{{ row.order_amount }}
        </template>
      </el-table-column>

      <!-- 支付状态列 -->
      <el-table-column label="支付状态" align="center" min-width="100">
        <template #default="{ row }">
          <el-tag v-if="row.pay_status === 0" type="info">已创建</el-tag>
          <el-tag v-else-if="row.pay_status === 4" type="warning">已打开</el-tag>
          <el-tag v-else-if="row.pay_status === 1" type="success">已支付</el-tag>
          <el-tag v-else-if="row.pay_status === 2" type="warning">已关闭</el-tag>
          <el-tag v-else-if="row.pay_status === 3" type="danger">已退款</el-tag>
        </template>
      </el-table-column>
      
      <!-- 分账状态列 -->
      <el-table-column label="分账状态" align="center" min-width="120">
        <template #default="{ row }">
          <span v-if="!row.royalty_status_text || row.royalty_status_text === '-'">-</span>
          <el-tag v-else-if="row.royalty_status_text === '待分账'" type="info">待分账</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账中'" type="warning">分账中</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账成功'" type="success">分账成功</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '单笔分账'" type="success">单笔分账</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '商家分账'" type="success">商家分账</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账失败'" type="danger">分账失败</el-tag>
          <span v-else>{{ row.royalty_status_text }}</span>
        </template>
      </el-table-column>

      <!-- 通知状态列 -->
      <el-table-column label="通知状态" align="center" min-width="100">
        <template #default="{ row }">
          <el-tag v-if="row.notify_status === 0" type="warning">未通知</el-tag>
          <el-tag v-else-if="row.notify_status === 1" type="success">通知成功</el-tag>
          <el-tag v-else-if="row.notify_status === 2" type="danger">通知失败</el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" align="center" min-width="160"/>
      <el-table-column prop="pay_time" label="支付时间" align="center" min-width="160"/>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="120">
        <template #default="scope">
          <el-dropdown @command="handleCommand" trigger="click">
            <el-button type="primary" size="small">
              操作 <el-icon class="el-icon--right"><ArrowDown /></el-icon>
            </el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item :command="{ action: 'detail', row: scope.row }">
                  <el-icon><View /></el-icon>查看详情
                </el-dropdown-item>
                <el-dropdown-item 
                  v-if="scope.row.pay_status === 1" 
                  :command="{ action: 'callback', row: scope.row }"
                  :disabled="!scope.row.notify_url"
                >
                  <el-icon><Bell /></el-icon>回调通知
                </el-dropdown-item>
                <el-dropdown-item :command="{ action: 'supplement', row: scope.row }">
                  <el-icon><Refresh /></el-icon>补单查询
                </el-dropdown-item>
                <el-dropdown-item divided>
                  <span style="color: #909399; font-size: 12px;">
                    状态：{{ getPayStatusText(scope.row.pay_status) }} | 
                    通知：{{ getNotifyStatusText(scope.row.notify_status) }}
                  </span>
                </el-dropdown-item>
                <el-dropdown-item v-if="scope.row.notify_times" divided>
                  <span style="color: #909399; font-size: 12px;">
                    回调次数：{{ scope.row.notify_times }} / 5
                  </span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

  <!-- 详情对话框 -->
  <el-dialog v-model="detailVisible" title="订单详情" width="800px">
    <el-descriptions :column="2" border v-if="currentOrder">
      <el-descriptions-item label="平台订单号">{{ currentOrder.platform_order_no }}</el-descriptions-item>
      <el-descriptions-item label="商户订单号">{{ currentOrder.merchant_order_no }}</el-descriptions-item>
      
      <el-descriptions-item label="订单金额">¥{{ currentOrder.order_amount }}</el-descriptions-item>
      <el-descriptions-item label="代理商">{{ currentOrder.agent?.agent_name || '-' }}</el-descriptions-item>
      <el-descriptions-item label="商户">{{ currentOrder.merchant?.merchant_name || '-' }}</el-descriptions-item>
      <el-descriptions-item label="产品">{{ currentOrder.product?.product_name || '-' }}</el-descriptions-item>
      <el-descriptions-item label="主体">{{ currentOrder.subject?.company_name || '-' }}</el-descriptions-item>
      <el-descriptions-item label="支付状态">
        <el-tag v-if="currentOrder.pay_status === 0" type="info">已创建</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 4" type="warning">已打开</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 1" type="success">已支付</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 2" type="warning">已关闭</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 3" type="danger">已退款</el-tag>
      </el-descriptions-item>
      <el-descriptions-item label="通知状态">
        <el-tag v-if="currentOrder.notify_status === 0" type="info">未通知</el-tag>
        <el-tag v-else-if="currentOrder.notify_status === 1" type="success">通知成功</el-tag>
        <el-tag v-else-if="currentOrder.notify_status === 2" type="danger">通知失败</el-tag>
      </el-descriptions-item>
      <el-descriptions-item label="通知次数">{{ currentOrder.notify_times }}</el-descriptions-item>
      <el-descriptions-item label="分账状态">
        <span v-if="!currentOrder.royalty_status_text || currentOrder.royalty_status_text === '-'">-</span>
        <el-tag v-else-if="currentOrder.royalty_status_text === '待分账'" type="info">待分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账中'" type="warning">分账中</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账成功'" type="success">分账成功</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '单笔分账'" type="success">单笔分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '商家分账'" type="success">商家分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账失败'" type="danger">分账失败</el-tag>
        <span v-else>{{ currentOrder.royalty_status_text }}</span>
      </el-descriptions-item>
      <el-descriptions-item label="购买者UID">{{ currentOrder.buyer_id || '-' }}</el-descriptions-item>
      <el-descriptions-item label="打开者IP">{{ currentOrder.first_open_ip || '-' }}</el-descriptions-item>
      <el-descriptions-item label="支付者IP">{{ currentOrder.pay_ip || '-' }}</el-descriptions-item>
      <el-descriptions-item label="客户端IP">{{ currentOrder.client_ip || '-' }}</el-descriptions-item>
      <el-descriptions-item label="创建时间">{{ currentOrder.created_at }}</el-descriptions-item>
      <el-descriptions-item label="支付时间">{{ currentOrder.pay_time || '-' }}</el-descriptions-item>
      <el-descriptions-item label="通知时间">{{ currentOrder.notify_time || '-' }}</el-descriptions-item>
      <el-descriptions-item label="关闭时间">{{ currentOrder.close_time || '-' }}</el-descriptions-item>
      <el-descriptions-item label="回调通知地址" :span="2">{{ currentOrder.notify_url || '-' }}</el-descriptions-item>
      <el-descriptions-item label="同步返回地址" :span="2">{{ currentOrder.return_url || '-' }}</el-descriptions-item>
      <el-descriptions-item label="备注" :span="2">{{ currentOrder.remark || '-' }}</el-descriptions-item>
    </el-descriptions>
  </el-dialog>
</template>

<script lang="ts" setup>
import { Refresh, Search, ArrowDown, View, Bell } from '@element-plus/icons-vue';
import { ref, onMounted, computed, watch } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 使用表格插件来管理表格的状态、数据等
const {
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/order/list");

// 搜索默认显示
const showSearch = ref(true);

// 切换搜索显示
const toggleSearchDisplay = () => {
  showSearch.value = !showSearch.value;
};

// 获取今天的日期
const getTodayDate = () => {
  const today = new Date();
  const year = today.getFullYear();
  const month = String(today.getMonth() + 1).padStart(2, '0');
  const day = String(today.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

// 获取今天的开始和结束时间
const getTodayRange = () => {
  const todayDate = getTodayDate();
  return [
    `${todayDate} 00:00:00`,
    `${todayDate} 23:59:59`
  ];
};

// 日期范围 - 默认设置为当天 00:00:00 到 23:59:59
const dateRange = ref(getTodayRange());

// 搜索表单数据
const searchForm = ref({
  agent_id: '',
  merchant_id: '',
  platform_order_no: '',
  merchant_order_no: '',
  
  pay_status: '',
  notify_status: '',
  start_time: getTodayRange()[0],
  end_time: getTodayRange()[1]
});

// 处理日期范围变化
const handleDateRangeChange = (value: any) => {
  if (value && value.length === 2) {
    searchForm.value.start_time = value[0];
    searchForm.value.end_time = value[1];
  } else {
    searchForm.value.start_time = '';
    searchForm.value.end_time = '';
  }
};

// 代理商列表
const agentList = ref([]);

// 商户列表
const merchantList = ref([]);

// 订单统计
const statistics = ref({
  total_count: 0,
  paid_count: 0,
  unpaid_count: 0,
  closed_count: 0,
  refunded_count: 0,
  total_amount: 0,
  paid_amount: 0,
  success_rate: 0,
  official_fee: 0, // 新增官方手续费
  last_minute_count: 0 // 新增一分钟订单数
});

// 详情对话框
const detailVisible = ref(false);
const currentOrder = ref(null);

// 获取用户信息判断是否是代理商
const userInfo = ref<any>({});
const isAgent = computed(() => {
  return userInfo.value?.is_agent === true;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取代理商列表（仅管理员需要）
const getAgentList = async () => {
  if (isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/order/agent-list',
      method: 'GET'
    });
    if (res.status) {
      agentList.value = res.data;
    }
  } catch (error) {
    console.error('获取代理商列表失败:', error);
  }
};

// 获取商户列表
const getMerchantList = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/order/merchant-list',
      method: 'GET',
      params: {
        agent_id: searchForm.value.agent_id
      }
    });
    if (res.status) {
      merchantList.value = res.data;
    }
  } catch (error) {
    console.error('获取商户列表失败:', error);
  }
};

// 获取订单统计
const getStatistics = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/order/statistics',
      method: 'GET',
      params: searchParams.value
    });
    if (res.status) {
      statistics.value = res.data;
    }
  } catch (error) {
    console.error('获取统计失败:', error);
  }
};

// 代理商变更时重新加载商户列表
const handleAgentChange = () => {
  searchForm.value.merchant_id = '';
  getMerchantList();
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
  await getStatistics();
};

// 处理重置
const handleReset = () => {
  const todayRange = getTodayRange();
  dateRange.value = todayRange;
  searchForm.value = {
    agent_id: '',
    merchant_id: '',
    platform_order_no: '',
    merchant_order_no: '',
    
    pay_status: '',
    notify_status: '',
    start_time: todayRange[0],
    end_time: todayRange[1]
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
  getStatistics();
};

// 下拉菜单命令处理
const handleCommand = (command: any) => {
  const { action, row } = command;
  switch (action) {
    case 'detail':
      handleViewDetail(row);
      break;
    case 'callback':
      handleRowCallback(row);
      break;
    case 'supplement':
      handleRowSupplement(row);
      break;
  }
};

// 获取支付状态文本
const getPayStatusText = (status: number) => {
  const statusMap: any = {
    0: '已创建',
    4: '已打开',
    1: '已支付',
    2: '已关闭',
    3: '已退款'
  };
  return statusMap[status] || '未知';
};

// 获取通知状态文本
const getNotifyStatusText = (status: number) => {
  const statusMap: any = {
    0: '未通知',
    1: '通知成功',
    2: '通知失败'
  };
  return statusMap[status] || '未知';
};

// 查看详情
const handleViewDetail = async (row: any) => {
  try {
    const res = await request({
      url: `/api/v1/admin/order/detail/${row.id}`,
      method: 'GET'
    });
    if (res.status) {
      currentOrder.value = res.data;
      detailVisible.value = true;
    }
  } catch (error) {
    console.error('获取详情失败:', error);
  }
};

// 处理回调（通过输入订单号）
const handleCallback = async () => {
  try {
    const { value: orderNo } = await ElMessageBox.prompt('请输入平台订单号', '手动回调', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      inputPlaceholder: '请输入平台订单号'
    });
    
    if (!orderNo) {
      ElMessage.warning('订单号不能为空');
      return;
    }
    
    try {
      const res: any = await request.post('/api/v1/admin/order/callback', { 
        platform_order_no: orderNo 
      });
      if (res.status) {
        ElMessage.success(res.msg || '回调成功');
        refreshData();
      } else {
        ElMessage.error(res.msg || '回调失败');
      }
    } catch (error: any) {
      ElMessage.error(error.response?.data?.msg || error.message || '回调失败');
    }
  } catch (error) {
    // 用户取消，不做处理
  }
};

// 处理补单（通过输入订单号）
const handleSupplementOrder = async () => {
  try {
    const { value: orderNo } = await ElMessageBox.prompt('请输入平台订单号', '手动补单', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      inputPlaceholder: '请输入平台订单号'
    });
    
    if (!orderNo) {
      ElMessage.warning('订单号不能为空');
      return;
    }
    
    // 确认操作
    try {
      await ElMessageBox.confirm(
        `确定要补单吗？系统将查询支付宝订单状态并更新本地订单。`,
        '确认补单',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      );
    } catch {
      // 用户取消
      return;
    }
    
    try {
      const res: any = await request.post('/api/v1/admin/order/supplement', { 
        platform_order_no: orderNo 
      });
      if (res.status) {
        ElMessage.success(res.msg || '补单成功');
        refreshData();
      } else {
        ElMessage.error(res.msg || '补单失败');
      }
    } catch (error: any) {
      ElMessage.error(error.response?.data?.msg || error.message || '补单失败');
    }
  } catch (error) {
    // 用户取消输入，不做处理
  }
};

// 已选中的行
const selectedRows = ref<any[]>([]);
const handleSelectionChange = (rows: any[]) => {
  selectedRows.value = rows;
};
// 单行回调
const handleRowCallback = async (row: any) => {
  if (!row.platform_order_no) return;
  if (!row.notify_url) {
    ElMessage.warning('该订单无回调地址，已跳过');
    return;
  }
  
  // 确认操作
  try {
    await ElMessageBox.confirm(
      `确定要回调订单 ${row.platform_order_no} 吗？`,
      '确认回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    );
  } catch {
    // 用户取消
    return;
  }
  
  try {
    const res = await request.post('/api/v1/admin/order/callback', { ids: [row.id] });
    if (res.status) {
      const result = res.data;
      if (result.success_count > 0) {
        ElMessage.success(`回调成功：${result.message || '回调已发送'}`);
      } else if (result.skipped_count > 0) {
        ElMessage.warning(`回调跳过：${result.results?.[0]?.message || '订单无回调地址'}`);
      } else {
        ElMessage.error(`回调失败：${result.results?.[0]?.message || '未知错误'}`);
      }
      refreshData();
    } else {
      ElMessage.error(res.msg || '回调失败');
    }
  } catch (error: any) {
    ElMessage.error(error.response?.data?.msg || error.message || '回调失败');
  }
};
// 单行补单
const handleRowSupplement = async (row: any) => {
  if (!row.platform_order_no) return;
  
  // 确认操作
  try {
    await ElMessageBox.confirm(
      `确定要补单吗？系统将查询支付宝订单状态并更新本地订单。`,
      '确认补单',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    );
  } catch {
    // 用户取消
    return;
  }
  
  try {
    const res = await request.post('/api/v1/admin/order/reissue', { ids: [row.id] });
    if (res.status) {
      const result = res.data;
      if (result.success_count > 0) {
        ElMessage.success(`补单成功：${result.message || '订单已更新并触发回调'}`);
      } else if (result.skipped_count > 0) {
        ElMessage.warning(`补单跳过：${result.results?.[0]?.message || '订单已支付或已关闭'}`);
      } else {
        ElMessage.error(`补单失败：${result.results?.[0]?.message || '未知错误'}`);
      }
      refreshData();
    } else {
      ElMessage.error(res.msg || '补单失败');
    }
  } catch (error: any) {
    ElMessage.error(error.response?.data?.msg || error.message || '补单失败');
  }
};
// 批量回调
const handleBatchCallback = async () => {
  if (!selectedRows.value.length) {
    ElMessage.warning('请先选择要回调的订单');
    return;
  }
  
  const valid = selectedRows.value.filter((r:any)=>!!r.notify_url);
  const skipped = selectedRows.value.length - valid.length;
  if (!valid.length) {
    ElMessage.warning('所选订单均无回调地址，已全部跳过');
    return;
  }
  
  // 确认操作
  try {
    await ElMessageBox.confirm(
      `确定要批量回调 ${valid.length} 个订单吗？${skipped > 0 ? `（${skipped} 个订单无回调地址将被跳过）` : ''}`,
      '确认批量回调',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }
    );
  } catch {
    // 用户取消
    return;
  }
  
  try {
    const res = await request.post('/api/v1/admin/order/callback', { ids: valid.map((r:any)=>r.id) });
    if (res.status) {
      const result = res.data;
      ElMessage.success(result.message || `批量回调完成：成功 ${result.success_count} 条，失败 ${result.failed_count} 条，跳过 ${result.skipped_count} 条`);
      refreshData();
    } else {
      ElMessage.error(res.msg || '批量回调失败');
    }
  } catch (error: any) {
    ElMessage.error(error.response?.data?.msg || error.message || '批量回调失败');
  }
};
// 批量补单
const handleBatchSupplement = async () => {
  if (!selectedRows.value.length) {
