    return;
  }
  
  try {
    const res = await request.post('/api/v1/admin/order/reissue', { ids: selectedRows.value.map((r:any)=>r.id) });
    if (res.status) {
      const result = res.data;
      ElMessage.success(result.message || `批量补单完成：成功 ${result.success_count} 条，失败 ${result.failed_count} 条，跳过 ${result.skipped_count} 条`);
      refreshData();
    } else {
      ElMessage.error(res.msg || '批量补单失败');
