  }
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getUserInfo();
  await getAgentList();
  await getMerchantList();
  
  // 设置初始搜索参数为当天
  searchParams.value = { ...searchForm.value };
  
  loadData();
  getStatistics();
